function create_pagination() {
    const pages = document.querySelectorAll('.pages')
    const paginationEndNumber = document.getElementById('pagination__end__number')
    const pageNumber = document.querySelectorAll('#pageNumber')

    for (let i = 0; i < pages.length; i++) {
        let div = document.createElement('div')
        paginationEndNumber.textContent = (i >= 9) ? `${i + 1}` : `0${i + 1}`
        pageNumber[i].textContent = (i >= 9) ? `${i + 1}` : `0${i + 1}`

        if (i == 0) {
            div.className = 'pagination__cont__items pagination__cont__items--active'
        } else {
            div.className = 'pagination__cont__items'
        }
        document.querySelector('.pagination__cont').appendChild(div)
    }
}

(function () {
    create_pagination()
    const
        next = document.getElementById("btn__next"),
        prev = document.getElementById("btn__prev"),

        slides = document.querySelectorAll(".pages"),
        dots = document.querySelectorAll(".pagination__cont__items");

    let paginationFirstNumber = document.getElementById('pagination__first__number')

    let index = 0;
    const activeSlide = n => {
        let slide
        for (slide of slides) {
            slide.classList.remove("pages--active");
            slide.classList.remove("pages__active__animTop__To__Bottom");
        }
        slides[n].classList.add("pages--active");
        slides[n].classList.add("pages__active__animTop__To__Bottom");
    }
    const activeDot = n => {
        let dot
        for (dot of dots) {
            dot.classList.remove("pagination__cont__items--active");
        }
        dots[n].classList.add("pagination__cont__items--active");
    }
    const nextSlide = () => {
        if (index == slides.length - 1) {
            index = 0;
            prepore_all(index);
        } else {
            index++;
            prepore_all(index);
        }
        paginationFirstNumber.textContent = (index >= 9) ? `${index + 1}` : `0${index + 1}`
    }
    const prevSlide = () => {
        if (index == 0) {
            index = slides.length - 1;
            prepore_all(index);
        } else {
            index--;
            prepore_all(index);
        }
        paginationFirstNumber.textContent = (index >= 9) ? `${index + 1}` : `0${index + 1}`
    }
    const prepore_all = ind => {
        activeSlide(ind);
        activeDot(ind);
    }
    dots.forEach((item, indexDot) => {
        item.addEventListener("click", () => {
            index = indexDot;
            prepore_all(index);
        })
    })
    next.addEventListener("click", nextSlide);
    prev.addEventListener("click", prevSlide);

    // window.addEventListener('wheel', (e) => {
    //     if (e.deltaY > 0) {
    //         nextSlide()
    //     } else {
    //         prevSlide()
    //     }
    // })
}())

function mobileBtn() {
    document.querySelectorAll('#menuBtnMobile').forEach((el, i) => {
        el.addEventListener('click', () => {
            document.querySelectorAll('.mibileNavigation')[i].classList.toggle('mibileNavigation--active')
        })
    })
}
mobileBtn()